/* ===========================================================

	# Fade in animations

=========================================================== */
@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.fadeIn {
  animation-name: fadeIn; }

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 2rem, 0); }
  to {
    opacity: 1;
    transform: none; } }

.fadeInUp {
  opacity: 1;
  transform: translate3d(0, 0, 0); }

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translate3d(5rem, 0, 0); }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.fadeInRight {
  animation-name: fadeInRight; }

/* ===========================================================

	# Fade in animations

=========================================================== */
@keyframes fadeOut {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

.fadeOut {
  animation-name: fadeOut; }

@keyframes fadeOutDown {
  from {
    opacity: 1; }
  to {
    opacity: 0;
    transform: translate3d(0, 2rem, 0); } }

.fadeOutDown {
  opacity: 0;
  transform: translate3d(0, 2rem, 0);
  transition-duration: .3s !important; }

@keyframes fadeOut {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

.fadeOut {
  animation-name: fadeOut; }

